@import url(https://fonts.googleapis.com/css2?family=Lato&family=Roboto&display=swap);
*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}@font-face{font-family:'icon';src:url(/static/media/fa-solid-900.6ac8eb0f.ttf);font-weight:900}.icon{font-family:'icon'}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.nav{display:flex;margin-top:10px}.nav__list{display:flex;align-items:center}.nav__link{display:block;font-size:18px;text-transform:capitalize;color:#000;padding:10px 20px}.nav__link:hover{background:#000;color:#fff}

.header__wrap{display:flex;align-items:center;justify-content:space-between}.header__person{display:block;font-size:26px;color:#000}.header{box-shadow:0 4px 0 0 #000}.header__logo{display:block;font-size:28px;text-transform:uppercase;font-style:italic;font-weight:900;color:#000}

.exchange{padding:40px 25px;border:1px solid #000}.exchange__head{font-size:25px}.exchange__content li{display:flex;align-items:center;justify-content:space-between;font-size:20px;border-bottom:1px solid #000;margin:10px 0;padding:7px 0}.exchange__sp1{display:flex;align-items:center}.exchange__sp2{display:flex;max-width:250px;width:100%;align-items:flex-start}.exchange img{width:30px}


.sidebar{border:1px solid #bfc4cd;max-width:350px;width:100%}.sidebar__head{border-bottom:1px solid #bfc4cd;display:flex;align-items:center;justify-content:center;padding:7px 0}.sidebar__content li{padding:7px 25px;border-bottom:1px solid #bfc4cd;display:flex;align-items:center}.sidebar__content li p{display:flex;align-items:center}.sidebar__content li span{font-weight:700}.sidebar__content li img{width:20px;height:15px}

.layout{max-width:1300px;width:100%;margin:0 auto;padding:0 20px;text-align:center}.content{display:flex;grid-gap:30px;gap:30px;margin-top:40px;padding:0 20px;justify-content:space-between;flex-wrap:wrap}.routes{max-width:750px;width:100%}

.calc{min-height:16vw;border:1px solid #bfc4cd}.calc__container{width:90%;margin:0 auto;padding:1vw 0}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none}

.btn{font-size:18px;color:#223046;border-radius:5px;padding:3px 10px}.btn:hover{color:#fff;background:#223046}

.operation{display:grid;grid-template-columns:2fr 1fr 0.5fr;grid-gap:30px;gap:30px}

.sample{min-height:16vw;border:1px solid #bfc4cd}.sample__container{width:90%;margin:0 auto;padding:1vw 0}.sample__container>div{margin-bottom:30px}.sample__head{width:70%;display:grid;grid-template-columns:2fr 1fr}.sample__result{margin:2vw 0}.sample__result li{padding-left:25%;border:1px solid #bfc4cd;display:block;margin-bottom:1vw;padding:0.5vw;display:grid;grid-template-columns:2fr 2fr 2fr .3fr}.sample__result li img{width:20px;height:15px}


.dark{position:fixed;width:100%;height:100%;top:0;background:#000;opacity:0.6;z-index:7;display:none}.darkShow{display:block}

.modal__btn{margin:3vw auto;display:flex;align-items:center;justify-content:center}

.modal__btn{margin:3vw auto;display:flex;align-items:center;justify-content:center}

.modal{max-width:300px;width:100%;height:25vw;background:#b9d6f3;opacity:1;z-index:9;position:absolute;margin:auto;left:0;top:0;bottom:0;right:0;border-radius:5px;display:none}.modal__head{padding:.7vw;display:flex;justify-content:space-between;align-items:center}.modal__head li{display:inline-block}.modal__head li a{color:#223046;font-size:16px}.modal__head span{font-weight:bold;font-size:22px;cursor:pointer}.modalShow{display:block}

.modal__input{margin:1vw auto;display:grid;padding:0.5vw;grid-template-columns:1fr}.modal__input label{color:#223046;font-size:16px;line-height:120%}.modal__input input{padding:5px 10px;width:100%;display:block}.modal__input span{color:#f01f30;font-size:14px;font-weight:bold;display:block;width:100%}.input__invalid label{color:#f01f30}

