.nav{
    display: flex;
    margin-top: 10px;
}
.nav__list{
    display: flex;
    align-items: center;
}
.nav__link{
    display: block;
    font-size: 18px;
    text-transform: capitalize;
    color: #000;
    padding: 10px 20px;
}
.nav__link:hover{
    background: #000;
    color: #fff;
}